<template>
  <div>
    <el-checkbox
      v-for="(item, index) in checkeds"
      v-model="item.checkout"
      :key="index"
      >{{ item.name }}</el-checkbox
    >
  <el-button @click="save" type="primary">保存~~</el-button> 
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkeds: [
        { name: "选择1", checkout: false },
        { name: "选择2", checkout: false },
        { name: "选择3", checkout: false },
        { name: "选择4", checkout: false },
        { name: "选择5", checkout: false },
        { name: "选择6", checkout: false },
      ],
    };
  },
  methods: {
    save() {
      let result = this.checkeds.some((item) => {
        return item.checkout;
      });
      if (result) {
        alert("有一个或者多个选中");
      } else {
        alert("没有一个选中");
      }
    },
  },
};
</script>

